          @import "@/assets/core";
        

@import "@/assets/variables";

.main {
  display: block;
  flex-grow: 1;
}

.row {
  display: flex;
  align-items: flex-start;
}

.pc {
  background: var(--secondary-bg);
  border-radius: 8px;
  padding: 10px;
  margin-left: 15px;
  flex: 0 0 200px;

  @include notebook {
    display: none;
  }

  &__title {
    color: var(--text-primary);
    font-size: 16px;
    font-weight: 600;
    margin-bottom: 15px;
  }

  &__button {
    background: #fff5f5;
    border: 1px solid rgba(250, 101, 101, 0.5);
    border-radius: 2px;
    padding: 5px 20px;
    margin-top: 10px;
    font-size: 14px;
    display: inline-block;
  }
}

.steps {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-top: 15px;

  @include notebook {
    justify-content: flex-start;
  }
}

.step {
  width: 32%;
  margin-bottom: 30px;
  display: flex;
  flex-direction: column;

  @include notebook {
    max-width: 300px;
    width: 100%;
    height: auto;
    margin-right: 15px;
  }

  &__screen {
    width: 100%;
    height: auto;
    margin-top: 10px;
  }

  &__header {
    display: flex;
    align-items: center;

    &:not(&_noal) {
      margin: auto 0;
    }
  }

  &__num {
    background: #525d66;
    height: 20px;
    flex: 0 0 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 100%;
    color: #fff;
    font-size: 12px;
    margin-right: 4px;
  }
}
